import React, { useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";

function App() {

  var [noteList, setNoteList] = useState([]);

  function addNote(note){
    setNoteList( pre =>{ noteList= [...pre, note]; return noteList;});
  }

  function deleteNote(id){
    setNoteList( (pre) =>
     pre.filter(
        (value, index)=> index !== id))
    }

  return (
    <div>
      <Header />
      <CreateArea onAdd={addNote}/>
      {noteList.map( (value,index) =>{
         return <Note key={index} id={index} title={value.title} content={value.content} onDelete={deleteNote} />
      })}
      <Footer />
    </div>
  );
}

export default App;
