import React, { useState } from "react";

function CreateArea(props) {

  var [item, setItem] = useState({
    title:"",
    content:""
  });


  function takeInput(event){
    var {value, name} = event.target;
    setItem( preValue => {
      return{
        ...preValue,
        [name]:value
      }
    });
  }

  function addItem(event){
    props.onAdd(item);
    event.preventDefault();
  }
  return (
    <div>
      <form>
        <input onChange={takeInput} name="title" placeholder="Title" value={item.title} />
        <textarea onChange={takeInput} name="content" placeholder="Take a note..." rows="3" value={item.content} />
        <button onClick={addItem}>Add</button>
      </form>
    </div>
  );
}

export default CreateArea;
